  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.contenedor_home .tarjetas .contenedor_tarjetas {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: flex-end;
  align-items: flex-end;
  gap: 0.01rem;
  position: absolute;
}
.contenedor_home .tarjetas .contenedor_tarjetas .tarjeta {
  min-width: 12rem;
  width: 15rem;
  height: 20rem;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  box-shadow: 1rem 1rem 1rem black;
  position: relative;
  cursor: pointer;
  z-index: 4;
  transition: 0.5s ease-out;
  margin-bottom: 13rem;
  margin-right: 3rem;
}
.contenedor_home .tarjetas .contenedor_tarjetas .tarjeta img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  z-index: 0;
  -o-object-fit: cover;
     object-fit: cover;
  z-index: 1;
}
