.contenedor_home .tarjetas .contenedor_tarjetas .tarjeta .data .subtitulo_tarjetas {
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: lighter;
  display: none;
}
.contenedor_home .tarjetas .contenedor_tarjetas .tarjeta .data a .button_link {
  display: none;
}
.contenedor_home .tarjetas .contenedor_tarjetas .activado {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
  cursor: default;
