.contenedor{
    width: 100%;
    height: auto;
    box-sizing: border-box;
    position: relative;
    padding: 0;
    margin: 0;
    .fondo_aire {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        z-index: -1;
    }
    .contenedor_content{
        width: 100%;
        height: auto;
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        
        .mapa_calidades{
            position: relative;
            top: 6rem;
            width: 100%;
            height: calc(100vh - 6rem);
            padding: 0;
            margin: 0;
            box-sizing: border-box;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-auto-rows: minmax(100px, auto);
            .seccion_mapa1{
                width: 100%;
                height: auto;
                position: relative;
                .contenedor_progress{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    .progress_bar {
                        position: relative;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        height: 12rem;
                        width: 90%;
                        background-color: #93f77a3d;
                        z-index: 2;
                        border-radius: 0px 55px 55px 0px;
                       
                        .air_quality_index{
                            
                            font-size: 25px;
                            font-weight: bold;
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            width: 150px;
                            height: 75px;
                            background: black;
                            border-radius: 150px 150px 0 0;
                            text-align: center;
                            border: 2px solid white;
                            z-index: 2;
                            .air_text{
                                font-size: 1rem;
                            }
                        }
                        
                    }
                }
                
            }
            .seccion_mapa2{
                grid-column: 2;
                width: 100%;
                height: 100%;
                padding-top: 2rem;
                z-index: 2;
                .map {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 55%;
                    
                }
                .search-bar{
                    width: 100%;
                    max-width: 700px;
                    background: rgba(255,255,255,0.2);
                    display: flex;
                    align-items: center;
                    border-radius: 60px;
                    padding: 10px 20px;
                    backdrop-filter: blur(4px) saturate(180%);
                    input {
                        background: transparent;
                        flex: 1;
                        border: 0;
                        outline: none;
                        padding: 24px 20px;
                        font-size: 20px;
                        color: #cac7ff;
                    }   
                    &::placeholder{
                        color: #cac7ff;
                    }
                    button{
                        border: 0;
                        border-radius: 50%;
                        width: 60px;
                        height: 60px;
                        background: #58629b;
                        cursor: pointer;
                        img{
                            width: 25px;
                        }
                    }
                }
                .date_city_search{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .datetime {
                        color: #fff;
                        background: #10101E;
                        font-family: "Segoe UI", sans-serif;
                        width: 340px;
                        padding: 15px 10px;
                        border: 3px solid #2E94E3;
                        border-radius: 5px;
                        -webkit-box-reflect: below 1px linear-gradient(transparent, rgba(255, 255, 255, 0.1));
                        transition: 0.5s;
                        transition-property: background, box-shadow;
                        &:hover {
                            background: #2E94E3;
                            box-shadow: 0 0 30px #2E94E3;
                        }
                        .date {
                            font-size: 20px;
                            font-weight: 600;
                            text-align: center;
                            letter-spacing: 3px;
                        }
                        .time {
                            font-size: 60px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            span:not(:last-child) {
                                position: relative;
                                margin: 0 6px;
                                font-weight: 600;
                                text-align: center;
                                letter-spacing: 3px;
                            }
                            .time span:last-child {
                                background: #2E94E3;
                                font-size: 30px;
                                font-weight: 600;
                                text-transform: uppercase;
                                margin-top: 10px;
                                padding: 0 5px;
                                border-radius: 3px;
                            }
                        }
                    }
                }
                
            }
            .seccion_mapa3{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                gap: 2rem 2rem;
                
                .contenedor_cuadroinfo{
                    max-height: 100%;
                    width: 100%;
                    height: auto;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                    gap: 4rem 2rem;
                    overflow-y:auto;
                    .cuadro_infosecundaria {
                        width: 7rem;
                        height: 10rem;
                        border-radius: 10% 10% 0 0;
                        position: relative;
                        cursor: pointer;
                        &:hover .texto_dato{
                            height: 65%;
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                            .titulo{
                                display: flex;
                                text-align: center;
                                justify-content: center;
                                align-items: center;
                                padding-top: 1rem;
                                opacity: 1;
                                transition: ease-in-out 2s;
                            }
                        }
                        &:hover .nro_dato{
                            height: 35%;
                        }
                        .nro_dato{
                            position: relative;
                            width: 100%;
                            height: 80%;
                            background-color: #ac9c5c70;
                            color: #fff;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            border-radius: 12% 12% 0 0 ;
                            text-shadow: 10px 10px 5px rgb(7, 7, 7);
                            font-size: 2rem;
                            font-weight: bolder;
                            transition: ease 1s;
                        }
                        .texto_dato{
                            position: relative;
                            width: 100%;
                            background-color: white;
                            height: 20%;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            border-radius: 0 0 20% 20%;
                            font-weight: bolder;
                            transition: ease 1s;
                            .titulo{
                                display: none;
                                color: gray;
                                opacity: 0;
                                transition: ease-in-out 1s;
                            }
                        }
                    }
                }
                
            }
            
        }
        .Contenedor_Datos1{
            width: 100%;
            height: 100vh;
            h1{
                color: white;
                
            }
            
        }
        .grafico{
            position: relative;
            top: 6rem;
        }
    }
}