header {
  height: 6rem;
  width: 100vw;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
}
header .titulo {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
header .titulo .logo {
  width: 4.5rem;
  height: 4.5rem;
}
header nav {
  display: flex;
  flex-direction: row;
  font-size: 1rem;
  gap: 4rem;
  height: 100%;
}
header nav .secciones_nav {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  align-items: center;
  gap: 6.5rem;
}
header nav .iconos_nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.5rem;
  font-size: 1rem;
  color: white;
  padding-left: 1.5rem;
}
header nav .iconos_nav .search {
  width: 22px;
  height: 22px;
}
header nav .iconos_nav .user {
  width: 22px;
  height: 22px;
}
header nav a {
  color: white;
  font-size: 0.9rem;
  font-weight: 600;
  align-items: center;
  text-decoration: none;
  transition: 0.9s ease;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
header nav a::after {
  content: "";
  position: absolute;
  width: 0px;
  height: 3px;
  transform: translateY(0.5rem);
  background-color: yellow;
  border-radius: 5px;
  transition: 0.6s ease;
}

.active::after {
  content: "";
  position: absolute;
  width: 3.5rem;
  height: 0.3rem;
  transform: translateY(0.5rem);
  background-color: yellow;
  border-radius: 5px;
}/*# sourceMappingURL=header.css.map */