@import url('https://fonts.googleapis.com/css2?family=Caramel&display=swap');

.contenedor_home{
    width: 100%;
    height: 100vh;
    display: flex;
    position: relative;
    .tarjetas{
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        .contenedor_tarjetas{
            display: flex;
            height: 100%;
            width: 100%;
            justify-content: flex-end;
            align-items: flex-end;
            gap: .01rem;
            position: absolute;
            
            .tarjeta{
                min-width: 12rem;
                width: 15rem;
                height: 20rem;
                border-radius: 1rem;
                display:flex;
                justify-content: center;
                align-items: flex-end;
                box-shadow: 1rem 1rem 1rem black;
                position: relative;
                cursor: pointer;
                z-index: 4;
                transition: 0.5s ease-out;
                margin-bottom: 13rem;
                margin-right: 3rem;
            img{
                    position: absolute;
                width: 100%;
                height: 100%;
                    border-radius: 1rem;
                    z-index: 0;
                    object-fit: cover;
                    z-index: 1;
            }
            .sombra_tarjeta{
                 position: absolute;
                background: linear-gradient(45deg, rgba(0, 0, 0, 0.973) 15%, transparent);
                height: 100%;
                width: 100%;
                border-radius: 1rem;
                z-index: 2;
                display: none;
            }
                .data{
                    z-index: 4;
                    width: 100%;
                    height: auto;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    align-items: flex-start;
                    color: white;
                    padding: 1rem;
                    .nro_tarjetas{
                        font-family: fantasy;
                        font-size: 3rem;
                        width: 100%;
                        display: none;
                    }
                    .titulo_tarjetas{
                        font-family:fantasy;
                        font-size: 1.7rem;
                    }
                    .subtitulo_tarjetas{
                        font-family: sans-serif;
                        font-size: 1rem;
                        font-weight: lighter;
                        display: none;
                    }
                    a{
                        .button_link {
                            display: none;
                        }
                    }
                    
                }
            }
            .activado{
                position: absolute;
                height: 100vh;
                width: 100vw;
                z-index: 1;
                cursor: default;
                margin-bottom: 0rem;
                margin-right: 0rem;
                .sombra_tarjeta{
                    display: block;
                }
                .data{
                    position: absolute;
                    left: 0;
                    width: 30%;
                    height: 100%;
                    justify-content: center;
                    margin-left: 2rem;
                    display: flex;
                    gap: 2rem;
                    .subtitulo_tarjetas {
                        display: block;
                        font-size: 1rem;
                        color: rgb(187, 185, 185);
                    }
                    .titulo_tarjetas {
                        font-family: 'Work Sans', sans-serif;
                        font-size: 3.3rem;
                        font-weight: 700;
                        width: 100%;
                        
                    }
                    .nro_tarjetas{
                        display: block;
                    }
                    a{
                        text-decoration: none;
                        .button_link {
                            position: relative;
                            display: block;
                            text-align: center;
                            background-color: none;
                            background-color: rgba(238, 248, 248, 0.116);
                            cursor: pointer;
                            width: 10rem;
                            height: 3rem;
                            border: 1px solid white;
                            border-radius: 20px;
                            color: white;
                        }
                    }
                    
                }
            }
        }
        .botones_tarjeta{
            right: 0;
            height: 15%;
            width: 65%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 1rem;
            position: absolute;
            z-index: 3;
            .button_next{
                font-size: 4.5rem;
                border: 1px solid white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
                cursor: pointer;
            }
            .button_prev{
                font-size: 4.5rem;
                border: 1px solid white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
                cursor: pointer;
            }
            hr{
                width: 55rem;
            }
            .nro_tarjeta{
                color: white;
                font-size: 30px;
                font-family: fantasy;
            }
        }
    }
}
