*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

body {
    background: #000;
}
.Contenedor{
    width: 100%;
    min-height: 100vh;
    position: relative;
    .contenido{
        z-index: 1;
        position: relative;
        height: auto;
    }
}